import { Component } from 'react';
import { Page } from './project';
import thisIcon from '../assets/projects/this.png';

const body = "testeroniiiiiii";

class Lumaki extends Component {
    constructor(props){ 
        super(props); 
        // this.state = { color : '#4cb96b' }; 
    }
    render () {
        return (
            <div>
                <Page 
                    icon={thisIcon} 
                    title="This website"
                    body={body}></Page>
            </div> 
        )
    }
}
export default Lumaki;
