import { Component } from 'react';
import { Page } from './project';
import fitrIcon from '../assets/projects/fitr.png';

const body = "testeroniiiiiii";

class Fitr extends Component {
    constructor(props){ 
        super(props); 
        // this.state = { color : '#4cb96b' }; 
    }
    render () {
        return (
            <div>
                <Page 
                    icon={fitrIcon} 
                    title="Fork in the road"
                    body={body}></Page>
            </div> 
        )
    }
}
export default Fitr;
